import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import test from "@/components/test"
import testTxt from "@/components/testTxt"

Vue.use(Router)

export default new Router({
	mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
    },
    {
    	path:'/test',
    	name : 'test',
    	component : test,
	    children:[
	    	{
	    		path: 'testTxt',
		      	name: 'testTxt',
		      	component: testTxt,
	    	}
	    ]
    }
  ]
})



