<template>
	<div class='circle' v-bind:class="{'scale':change_scale}" @click='_click'>{{msg}}</div>
</template>

<script>
	export default{
		name:'testTxt',
		data(){
			return {
				msg:'按钮长按测试',
				change_scale:false
			};
		},
		methods:{
			_click:function(){
				var _this = this;
				_this.change_scale = !this.change_scale;
				setTimeout(function(){
					_this.change_scale = !_this.change_scale;
				},300);
			}
		}
	}
</script>

<style scoped>
	.circle{
		width:100px;
		height:100px;
		margin: 0 auto;
		border-radius: 100px;
		background: red;
		color:#fff;
		font-size: 15px;
		text-align: center;
		overflow: hidden;
		line-height: 100px;
		transition:all 0.3s;
	}
	.scale{
		transform:scale(0.8);
	}
</style>


